import { Component, OnInit } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service';
import { PayToolBean } from './bean/pay-tool-manage.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
//
@Component({
  selector: 'app-pay-tool-manage',
  templateUrl: './pay-tool-manage.component.html',
  styleUrls: ['./pay-tool-manage.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class PayToolManageComponent implements OnInit {
  //
  tableData: any = [];
  // bean
  payToolBean: PayToolBean = new PayToolBean();
  //
  headerTitle: string = "";
  //
  display: boolean = false;
  showModel: string;
  //
  updateValue: any = [];
  //
  first: number = 0;
  total: number; //
  //
  msgs: Message[] = [];

  constructor(
    public orderHttpService: OrderHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    this.payToolBean.pageParam.pageSize = 10;
    this.payToolBean.pageParam.pageNumber = 1;
  }

  ngOnInit() {
    //
    this.query();
    //
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }

  //
  queryClick() {
    this.first = 0;
    this.payToolBean.pageParam.pageNumber = 1;
    this.query();
  }

  //
  query() {
    this.orderHttpService.searchPaymentTool(this.payToolBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.paymentToolList;
        this.total = data.totalCounts;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }

  //
  addClick() {
    this.headerTitle = '';
    this.display = true;
    this.showModel = '1';
  }

  //
  addCall(param) {
    this.display = param;
    this.query();
  }

  //
  modifyClick(item) {
    this.headerTitle = '';
    this.display = true;
    this.showModel = '2';
    this.updateValue = item.paymentMethodTypeId;
  }

  //
  updateCall(param) {
    this.display = param;
    this.query();
  }

  //
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        //
        let param = {
          paymentMethodTypeId: item.paymentMethodTypeId
        };
        this.orderHttpService.deletePaymentTool(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.queryClick();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
      }
    });
  }

  //
  paginate(event) {
    //
    this.payToolBean.pageParam.pageSize = event.rows * 1;
    //
    let currentPage = event.page + 1;
    this.payToolBean.pageParam.pageNumber = currentPage;
    this.first = event.page * event.rows;
    //
    this.query();
  }

  //
  resetClick() {
    this.payToolBean = new PayToolBean();
    this.payToolBean.pageParam.pageSize = 10;
    this.payToolBean.pageParam.pageNumber = 1;
  }
  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}

