import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { OrderHttpService } from './../../../../http/order.http.service';
import { ToolBehaveBean } from './../bean/tool-behave-relation.bean';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmationService } from 'primeng/primeng';

@Component({
    selector: 'tool-behave-add',
    styleUrls: ['tool-behave-add.component.scss'],
    templateUrl: 'tool-behave-add.component.html',
    providers: [Commfunc,ConfirmationService]
})

export class ToolBehaveAdd implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
     userform: FormGroup; //
    //
     toolBehaveBean: ToolBehaveBean = new ToolBehaveBean();
    //
     msgs: Message[] = [];
    //
     optionTool: any = []; //
     optionBehave: any = []; //

    constructor(
        public fb: FormBuilder,
        public orderHttpService: OrderHttpService,
        public commfunc: Commfunc
    ) {

    }

    ngOnInit() {
        this.userform = this.fb.group({
            'paymentMethodTypeId': new FormControl('', Validators.required),
            'enumerationId': new FormControl('', Validators.required),
            'description': new FormControl('', Validators.required),
        });

        //
        this.orderHttpService.searchAllPaymentTool({}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.optionTool = this.commfunc.toChangeTree(data.paymentToolList, "paymentMethodTypeName", "paymentMethodTypeId");
                this.optionTool.unshift({ label: "", value: '' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })

        //
        this.orderHttpService.searchAllPaymentBehavior({}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.optionBehave = this.commfunc.toChangeTree(data.paymentBehaviorList, "enumName", "enumId");
                this.optionBehave.unshift({ label: "", value: '' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
    }

    addSaveBtn() {
        //
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }

        //
        this.orderHttpService.addPaymentMethodTypeEnumeration(this.toolBehaveBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.toolBehaveBean = new ToolBehaveBean();
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
    }
    
    //
    addCloseBtn() {
        this.outValue.emit(false);
    }
}