import { Component, OnInit } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service'
import { ToolChannelBean } from './bean/tool-channel-relation.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

//
@Component({
  selector: 'app-tool-channel-relation',
  templateUrl: './tool-channel-relation.component.html',
  styleUrls: ['./tool-channel-relation.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class ToolChannelRelationComponent implements OnInit {
  //
   tableData: any = [];
  //
   optionTool: any = [];  //
   optionChan: any = []; //
  //bean
   toolChannelBean: ToolChannelBean = new ToolChannelBean();
  //
   headerTitle: string = "";//
   display: boolean = false;//
   showModel: string;//
  //
   updateValue: any = [];
  //
   total: string; //
   first: number = 0;
  //
   msgs: Message[] = [];

  constructor(
    public orderHttpService: OrderHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    this.toolChannelBean.pageSize = 10;
    this.toolChannelBean.pageNum = 1;
  }

  ngOnInit() {
    //
    this.query();


    //
    this.orderHttpService.searchAllPaymentTool({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.optionTool = this.commfunc.toChangeTree(data.paymentToolList, "paymentMethodTypeName", "paymentMethodTypeId");
        this.optionTool.unshift({ label: "", value: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })

    //
    this.orderHttpService.findAllPaymentGatewayConfig({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.optionChan = data.returnList;
        this.optionChan.unshift({ label: "", value: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })

  }

  //
  queryClick() {
    this.first = 0;
    this.toolChannelBean.pageNum = 1;
    this.query();
  }
  //
  query() {
    this.orderHttpService.findByConPaymentMethodTypeGatewayConfig(this.toolChannelBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.returnList;
        this.total = data.totalCounts;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }

  //
  addClick() {
    this.headerTitle = '';
    this.display = true;
    this.showModel = '1';
  }
  //
  addCall(param) {
    this.display = param;
    this.query();
  }

  //
  deleteClick(car) {
    //
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        //
        let param = {
          paymentMethodTypeId: car.paymentMethodTypeId,//
          paymentGatewayConfigId: car.paymentGatewayConfigId,//
        };
        this.orderHttpService.deletePaymentMethodTypeGatewayConfig(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.queryClick();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
      }
    });

  }

  //
  paginate(event) {
    //
    this.toolChannelBean.pageSize = event.rows * 1;
    //
    let currentPage = event.page + 1;
    this.toolChannelBean.pageNum = currentPage;
    this.first = event.page * event.rows;
    //
    this.query();
  }

  //
  resetClick() {
    this.toolChannelBean = new ToolChannelBean();
    this.toolChannelBean.pageSize = 10;
    this.toolChannelBean.pageNum = 1;
  }

}
