import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { OrderHttpService } from './../../../../http/order.http.service';
import { PayChannelBean } from './../bean/pay-channel-manage.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// 
@Component({
    selector: 'pay-channel-add',
    styleUrls: ['pay-channel-add.component.scss'],
    templateUrl: 'pay-channel-add.component.html',
    providers: [Commfunc, ConfirmationService]
})

export class PayChannelAdd implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //
    userform: FormGroup;
    //
    payChannelBean: PayChannelBean = new PayChannelBean();
    //
    msgs: Message[] = [];

    constructor(
        public fb: FormBuilder,
        public orderHttpService: OrderHttpService,
        public commfunc: Commfunc
    ) {
    }

    ngOnInit() {
        this.userform = this.fb.group({
            'paymentGatewayConfigId': new FormControl('', [Validators.required, Validators.maxLength(20)]),
            'description': new FormControl('', [Validators.required, Validators.maxLength(32)]),
            'configDescription': new FormControl('', [Validators.required, Validators.maxLength(60)]),
            'configIp': new FormControl('', Validators.required),
            'configDk': new FormControl('', Validators.required),
            'configXy': new FormControl('', [Validators.required, Validators.pattern(/^[a-zA-Z]{1,20}$/)]),
            // 'configService': new FormControl('', [Validators.required, Validators.maxLength(32)]),
        });
    }

    //
    addSaveBtn() {
        //
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        //
        this.orderHttpService.addPaymentGatewayConfig(this.payChannelBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.payChannelBean = new PayChannelBean();
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }

    //
    addCloseBtn() {
        this.outValue.emit(false);
    }
}