import { Component, OnInit } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service';
import { PayRelationBean } from './bean/pay-relation-manage.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

//
@Component({
    selector: 'app-pay-relation',
    templateUrl: './pay-relation-manage.component.html',
    styleUrls: ['./pay-relation-manage.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class PayRelationManageComponent implements OnInit {
    //
    optionTool: any = [];//
    optionBehave: any = [];//
    optionChan: any = [];//
    //
    tableData: any = [];
    // bean
    payRelationBean: PayRelationBean = new PayRelationBean();
    //
    headerTitle: string = "";//  
    display: boolean = false;//
    showModel: string;//
    //
    updateValue: any = [];
    //
    total: string; //
    first: number = 0;
    //
    msgs: Message[] = [];

    constructor(
        public orderHttpService: OrderHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc
    ) {
        this.payRelationBean.pageParam.pageSize = 10;
        this.payRelationBean.pageParam.pageNumber = 1;
    }

    ngOnInit() {
        //
        this.query();

        //
        this.orderHttpService.searchAllPaymentTool({}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.optionTool = this.commfunc.toChangeTree(data.paymentToolList, "paymentMethodTypeName", "paymentMethodTypeId");
                this.optionTool.unshift({ label: "", value: '' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })

        //
        this.orderHttpService.searchAllPaymentBehavior({}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.optionBehave = this.commfunc.toChangeTree(data.paymentBehaviorList, "enumName", "enumId");
                this.optionBehave.unshift({ label: "", value: '' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })

        //
        this.orderHttpService.findAllPaymentGatewayConfig({}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.optionChan = data.returnList;
                // this.optionChan = this.commfunc.toChangeTree(data.returnList, "description", "paymentGatewayConfigId");
                this.optionChan.unshift({ label: "", value: '' });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
        //
        let _thiss = this;
        document.onkeydown = function (event) {
            if (event && event.keyCode === 13) {
                _thiss.queryClick();
            }
        }
    }

    //
    queryClick() {
        this.payRelationBean.pageParam.pageNumber = 1;
        this.first = 0;
        this.query();
    }
    //
    query() {
        this.orderHttpService.searchPaymentMethodTypeEnumerationGateway(this.payRelationBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.paymentInfolList;
                this.total = data.totalCounts + "";
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
    }

    //
    addClick() {
        this.headerTitle = '';
        this.display = true;
        this.showModel = '1';
    }

    //
    addCall(param) {
        this.display = param;
        this.query();
    }

    //
    modifyClick(item) {
        this.headerTitle = '';
        this.display = true;
        this.showModel = '2';
        this.updateValue = item;
    }

    //
    updateCall(param) {
        this.display = param;
        this.query();
    }

    //
    deleteClick(item) {
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                //
                let param = {
                    paymentMethodTypeId: item.paymentMethodTypeId,
                    paymentGatewayConfigId: item.paymentGatewayConfigId,
                    paymentServiceTypeEnumId: item.paymentServiceTypeEnumId,
                };
                this.orderHttpService.delPaymentMethodTypeEnumerationGateway(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.queryClick();
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }
        });
    }

    //
    paginate(event) {
        //
        this.payRelationBean.pageParam.pageSize = event.rows * 1;
        //
        let currentPage = event.page + 1;
        this.payRelationBean.pageParam.pageNumber = currentPage;
        this.first = event.page * event.rows;
        //
        this.query();
    }

    //
    resetClick() {
        this.payRelationBean = new PayRelationBean();
        this.payRelationBean.pageParam.pageSize = 10;
        this.payRelationBean.pageParam.pageNumber = 1;
    }
    //
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
}

