import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { OrderHttpService } from './../../../../http/order.http.service';
import { Message, ConfirmationService } from 'primeng/primeng';
import { PayBehaveBean } from './../bean/pay-behave-manage.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// 
@Component({
    selector: 'pay-behave-modify',
    styleUrls: ['pay-behave-modify.component.scss'],
    templateUrl: 'pay-behave-modify.component.html',
    providers: [Commfunc, ConfirmationService]
})

export class PayBehaveModify implements OnChanges {
    @Input() inValue: string;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //
    userform: FormGroup;
    //
    msgs: Message[] = [];
    //
    payBehaveBean: PayBehaveBean = new PayBehaveBean();
    msgs0: any = [];
    constructor(
        public fb: FormBuilder,
        public orderHttpService: OrderHttpService,
        public commfunc: Commfunc
    ) {

    }

    ngOnInit() {
        this.userform = this.fb.group({
            'enumId': new FormControl('', Validators.required),
            'enumName': new FormControl('', [Validators.required, Validators.maxLength(32)]),
            'description': new FormControl('', [Validators.required, Validators.maxLength(60)])
        });
    }

    ngOnChanges() {
        let param = { enumId: this.inValue };
        this.orderHttpService.searchPaymentBehavior(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.payBehaveBean = data.paymentBehaviorList[0];
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        });
    }

    //
    modifySaveBtn() {
        //
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }

        //
        this.orderHttpService.updatePaymentBehavior(this.payBehaveBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs0 = [];
                this.msgs0.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                this.outValue.emit({ display: false, msgs: this.msgs0 });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        });

    }

    //
    modifyCloseBtn() {
        this.outValue.emit({ display: false });
    }
}