import { Component, OnInit } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service';
import { ToolBehaveBean } from './bean/tool-behave-relation.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
//
@Component({
  selector: 'app-tool-behave-relation',
  templateUrl: './tool-behave-relation.component.html',
  styleUrls: ['./tool-behave-relation.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class ToolBehaveRelationComponent implements OnInit {
  //
  optionTool: any = []; //
  optionBehave: any = [];  //
  //
  tableData: any = [];
  //bean
  toolBehaveBean: ToolBehaveBean = new ToolBehaveBean();
  //
  headerTitle: string = "";//
  display: boolean = false; //
  showModel: string;//
  //
  updateValue: string;
  //
  total: string; //
  first: number = 0;
  //
  msgs: Message[] = [];


  constructor(
    public orderHttpService: OrderHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    this.toolBehaveBean.pageSize = 10;
    this.toolBehaveBean.pageNum = 1;
  }

  ngOnInit() {
    //
    this.query();

    //
    this.orderHttpService.searchAllPaymentTool({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.optionTool = this.commfunc.toChangeTree(data.paymentToolList, "paymentMethodTypeName", "paymentMethodTypeId");
        this.optionTool.unshift({ label: "", value: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })

    //
    this.orderHttpService.searchAllPaymentBehavior({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.optionBehave = this.commfunc.toChangeTree(data.paymentBehaviorList, "enumName", "enumId");
        this.optionBehave.unshift({ label: "", value: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  queryClick() {
    this.toolBehaveBean.pageNum = 1;
    this.first = 0;
    this.query();
  }

  //
  query() {
    this.orderHttpService.findByConPaymentMethodTypeEnumeration(this.toolBehaveBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.returnList;
        this.total = data.totalCounts;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }


  //
  addClick() {
    this.headerTitle = '';
    this.display = true;
    this.showModel = '1';
  }
  //
  addCall(param) {
    this.display = param;
    this.query();
  }


  //
  deleteClick(item) {
    //
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        //
        let param = {
          paymentMethodTypeId: item.paymentMethodTypeId,
          enumerationId: item.enumerationId,
        };
        this.orderHttpService.deletePaymentMethodTypeEnumeration(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.query();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
      }
    });
  }


  //
  paginate(event) {
    //
    this.toolBehaveBean.pageSize = event.rows * 1;
    //
    let currentPage = event.page + 1;
    this.toolBehaveBean.pageNum = currentPage;
    this.first = event.page * event.rows;
    //
    this.query();
  }


  //
  resetClick() {
    this.toolBehaveBean = new ToolBehaveBean();
    this.toolBehaveBean.pageSize = 10;
    this.toolBehaveBean.pageNum = 1;
  }
}
