import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { OrderHttpService } from './../../../../http/order.http.service';
import { PayToolBean } from './../bean/pay-tool-manage.bean';
import { Message,ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// 
@Component({
    selector: 'pay-tool-add',
    styleUrls: ['pay-tool-add.component.scss'],
    templateUrl: 'pay-tool-add.component.html',
    providers:[Commfunc,ConfirmationService]
})

export class PayToolAdd implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //
     userform: FormGroup;
    //
     payToolBean: PayToolBean = new PayToolBean();
    //
     msgs: Message[] = [];

    constructor(
        public fb: FormBuilder,
        public orderHttpService: OrderHttpService,
        public commfunc:Commfunc
    ) {

    }

    ngOnInit() {
        this.userform = this.fb.group({
            'paymentMethodTypeId': new FormControl('', [Validators.required,Validators.maxLength(20)]),
            'paymentMethodTypeName':new FormControl('', [Validators.required,Validators.maxLength(32)]),
            'description': new FormControl('', [Validators.required,Validators.maxLength(60)])
        });
    }

    //
    addSaveBtn() {
        //
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }

        //
        this.orderHttpService.addPaymentTool(this.payToolBean).subscribe(data => {
            if (data.returnCode.code=='success') {
                this.payToolBean = new PayToolBean();
                this.outValue.emit(false);
                this.msgs=[];
                this.msgs.push({severity:'success',summary:'',detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail:data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }

    //
    addCloseBtn() {
        this.outValue.emit(false);
    }
}