import { Component, OnInit } from '@angular/core';
import { MenubarModule, MenuItem } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
//
@Component({
  selector: 'app-pay-manage',
  templateUrl: './pay-manage.component.html',
  styleUrls: ['./pay-manage.component.scss'],
	providers: [Commfunc]
})
export class PayManageComponent implements OnInit {

  items1: MenuItem[];
  items2: MenuItem[];
  items3: MenuItem[];
  items4: MenuItem[];
  show:number;
  constructor(public commfunc:Commfunc) { }

  ngOnInit() {
    this.items1=[
      {
        label: '',
        routerLink:'/pages/tzb/order/order-center/pay-manage/pay-channel'
      }
    ];
    this.items2=[
      {
        label: '',
        routerLink:'/pages/tzb/order/order-center/pay-manage/pay-tool'
      }
    ];
    this.items3=[
      {
        label: '',
        routerLink:'/pages/tzb/order/order-center/pay-manage/pay-behave'
      }
    ];
    this.items4=[
      {
        label: '',
        routerLink:'/pages/tzb/order/order-center/pay-manage/pay-relation'
      }
    ]
  this.judgStatu()
  }
  judgStatu () {
    let url = window.location.href;
    let urlSplit = url.split('/');
    urlSplit.map(v => {
      if (v === 'pay-channel') {
        this.show = 1;
      }else if (v === 'pay-tool') {
        this.show = 2;
      } else if (v === 'pay-behave') {
        this.show = 3;
      }else if (v === 'pay-relation') {
        this.show = 4;
      }
    })
  }
apply(num) {
    this.show = num;
  }
  //
	permissionCheck(id){
		return	this.commfunc.permissionCheck(id)
	  }

}
