import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//
import { PayManageComponent } from './pay-manage.component';//
import { PayChannelManageComponent } from './pay-channel-manage/pay-channel-manage.component';//
import { PayToolManageComponent } from './pay-tool-manage/pay-tool-manage.component';//
import { PayBehaveManageComponent } from './pay-behave-manage/pay-behave-manage.component';//
import { ToolChannelRelationComponent } from './tool-channel-relation/tool-channel-relation.component';//
import { ToolBehaveRelationComponent } from './tool-behave-relation/tool-behave-relation.component';//
import { PayRelationManageComponent } from './pay-relation-manage/pay-relation-manage.component';//

const routes: Routes = [
    {
        path: '',
        component: PayManageComponent,
        children: [
            { path: '', redirectTo: 'pay-channel', pathMatch: 'full' },
            {
                path: 'pay-channel', component: PayChannelManageComponent,
            },
            {
                path: 'pay-tool', component: PayToolManageComponent,
            },
            {
                path: 'pay-behave', component: PayBehaveManageComponent,
            },
            {
                path: 'tool-channel-relation', component: ToolChannelRelationComponent,
            },
            {
                path: 'tool-behave-relation', component: ToolBehaveRelationComponent,
            },
            {
                path: 'pay-relation', component: PayRelationManageComponent,
            },
        ]
    },
];

export const PayManageRouting = RouterModule.forChild(routes);